.a-button {
	--_padding-block: 0.5em;
	--_padding-block-start: calc(0.5em);
	--_padding-block-end: calc(0.5em - 1rem / 16);
	--_padding-inline: 0.75em;

	display: inline-block;
	padding-inline: var(--_padding-inline);
	padding-block: var(--_padding-block-start) var(--_padding-block-end);
	text-decoration: none;
	font: var(--font-label-medium);
	color: var(--color-brand-blue);
	border: var(--border-size-medium) solid currentcolor;
	border-radius: calc(1lh + var(--_padding-block) * 2);
	background-color: var(--color-background-primary);
	transition-duration: var(--duration-xsmall);
	transition-property: background-color, color, border-color;

	&[data-shape=circle] {
		padding-inline: var(--_padding-block);
		border-radius: 50%;
		text-align: center;
		min-inline-size: calc(1lh + var(--_padding-block) * 2 + var(--border-size-medium) * 2);
	}

	&[data-size=small] {
		--_padding-block: var(--space-100);
		--_padding-inline: var(--space-200);

		font: var(--font-label-small);
	}
	&[data-size=large] {
		font: var(--font-label-large);
	}

	&[data-kind=solid] {
		color: var(--color-white);
		border-color: var(--color-brand-orange);
		background-color: var(--color-brand-orange);
	}
	&[data-kind=transparent] {
		color: var(--color-brand-rose);
		background-color: transparent;
	}
	&[data-kind=transparent-dark] {
		color: currentcolor;
		background-color: transparent;
	}

	&[data-variant=bubble] {
		color: var(--color-brand-blue);
		border-color: var(--color-brand-blue);
		background-color: transparent;
		transition-duration: var(--duration-medium);
		font-size: 0.85em;
	}

	&[disabled] {
		color: var(--color-content-tertiary);
		border-color: currentcolor;
	}


	@media (any-pointer: fine) {
		:where(a, button):where(:focus-visible, :hover):not([disabled]) &,
		&:where(:focus-visible, :hover):not([disabled]) {
			color: var(--color-content-primary-inverse);
			border-color: var(--color-brand-blue);
			background-color: var(--color-brand-blue);
			transition-timing-function: ease-out;

			&[data-kind=transparent] {
				color: var(--color-content-primary);
				border-color: var(--color-brand-rose);
				background-color: var(--color-brand-rose);
			}
			&[data-kind=transparent-dark] {
				color: var(--color-white);
				border-color: var(--color-brand-purple);
				background-color: var(--color-brand-purple);
			}
			&[data-variant=bubble] {
				color: var(--color-white);
				border-color: var(--color-brand-blue);
				background-color: var(--color-brand-blue);
			}
		}
	}
}
